<template>

    

<v-footer color="#CACACA" >
<v-card height="100">
    
</v-card>
<v-spacer></v-spacer>

<v-card outlined class="transparent mt-12 pt-12" width="1500">
  <v-divider></v-divider>
</v-card>
<v-spacer></v-spacer>
<v-spacer></v-spacer>

    <v-card outlined class="transparent"> 

<v-row>
    
   <v-spacer></v-spacer>
    <v-col v-for="(item, index) in footCard" :key=index :cols="scrnSize()">
         
        <v-card outlined width="500" height="250" class="transparent pl-12">
                
