            <v-card outlined class="transparent" height="40">
                
            </v-card>
       
            <div class="font-weight-bold">
                {{item.subTitle}}
            </div>
            
           
            <div>
                {{item.subText}}
            </div>
            <div>
                {{item.timeText}}
            </div>
                        <div class="font-weight-bold">
                {{item.subTitle2}}
                <v-btn min-width="200" outlined v-if=item.vbtn :class="item.vbtnopt">
                    {{item.btnText}} <v-spacer></v-spacer><v-icon v-if=item.vic>mdi-send</v-icon>
                </v-btn>
            </div>
            <div>
                {{item.subText2}}
                <v-btn outlined v-if=item.vbtn2 :class="item.vbtnopt2">
                    {{item.btnText2}}
                </v-btn>
            </div>
            <div>
                {{item.timeText2}}
            </div>
                        <div>
                {{item.subText3}}
                <v-btn v-if=item.vbtn3 outlined :class="item.vbtnopt2">
                    
                    {{item.btnText3}}
                    
                </v-btn>
            </div>
                        <div class="font-weight-bold">
                {{item.subTitle3}}
            </div>
                                    <div>
                {{item.subText4}}
            </div>
            </v-card>
         
    </v-col>
   <v-spacer></v-spacer>
</v-row>

   </v-card>
<v-spacer></v-spacer>
</v-footer>

</template>
<script>
export default {
    name:"Footer",
    data: () => ({
        footCard: [
            {text:"CONTACT US", subTitle:"Address", subText:"7th Circle, Amman, Jordan", subTitle2: "Phone:", subText2:"+962 6 5822626", subText3:"+962 79 9451777", subTitle3: "E-mail:", subText4:"Info@shamiderma.com", vbtnopt2:"transparent", vbtnopt:"transparent" },
            {text: "INFO", btnText:"Our Doctors", route:"/doctors", btnText2:"Our Services", route2:"services", btnText3:"Location", route3:"/location", vbtn:true, vbtn2:true, vbtn3:true, vbtnopt2:"pl-0 transparent", vbtnopt:"pl-0 transparent"},
            {text:"OPENNING HOURS", subText:"Sunday - Thursday", timeText:"9:00 - 19:00", subText2:"Friday - Saturday", timeText2:"9:00 - 16:00", vbtnopt:"transparent", vbtnopt2:"transparent"},
            {text:"APPOINTEMENT", btnText:"Book Now!", route:"/contact", vbtn:true, vbtnopt2:"transparent", vic:true}
        ]
    }),
    methods: {
        scrnSize(){
            if(window.innerWidth < 1200){
                return 12
            } else {
                return 2
            }
        }
    },
}
</script>
<style scoped>
.my-style { 
font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
font-size: 30px;
}  

.preSpace {
  white-space: pre;
}

</style>